        {src: './static/library/pexels-guilherme-rossi-2553425.jpg', alt: ''},
        {src: './static/library/pexels-mark-cruzat-3494806.jpg', alt: ''},
        {src: './static/library/pexels-photo-6748028.jpeg', alt: ''}
      ],
      count: 0
