<style lang="scss" scoped>
.container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  box-sizing: border-box;
  padding: 1rem 10rem;
}
.container-left {
  margin-right: 3rem;
  flex: 1;
  min-width: 320px;
  max-width: 385px;
}

.container-right {
